

































































import * as echarts from '@/common/js/echarts.simple.min.js';
import mpvueEcharts from '@/components/mpvue-echarts/src/echarts.vue';

var colors = ['#5BA0F2', '#FF9C89', '#A2DE65', '#C1BFFE', '#FFC24E', '#6ED2BA'];

function getPieOption() {
	return {
		color: colors,
		series: [{
			name: '访问来源',
			type: 'pie',
			radius: ['50%', '80%'],
			label: {
				normal: {
					show: false,
					position: 'center'
				},
			},
			data: [{
					value: 335,
					name: '直接访问',
				},
				{
					value: 310,
					name: '邮件营销',
				},
				{
					value: 234,
					name: '联盟广告'
				},
				{
					value: 135,
					name: '视频广告'
				},
				{
					value: 1548,
					name: '搜索引擎'
				}
			]
		}]
	};
}

function getLineOption() {
	return {
		dataset: {
			source: [
				['product', '2015', '2016'],
				['一月', 43.3, 85.8],
				['二月', 43.3, 85.8],
				['三月', 43.3, 85.8],
				['四月', 43.3, 85.8],
			]
		},
		grid: {
			top: '10%',
			left: '15%',
			right: '10%',
			bottom: '20%'
		},
		xAxis: {
			type: 'category',
			offset: 5,
			axisLine: {
				show: false
			},
			axisTick: {
				show: false
			},
			axisLabel: {
				fontSize: 12,
				color: '#8B8B8B',
			}
		},
		yAxis: {
			splitLine: {
				lineStyle: {
					color: '#F0F0F0'
				}
			},
			offset: 10,
			axisLine: {
				show: false
			},
			axisTick: {
				show: false
			},
			axisLabel: {
				fontSize: 12,
				color: '#8B8B8B',
			}
		},
		series: [{
				type: 'bar',
				itemStyle: {
					color: '#5BA0F2'
				},
				barWidth: '10',
			},
			{
				type: 'bar',
				itemStyle: {
					color: '#A2DE65'
				},
				barWidth: '10'
			},
		]
	};
}

export default {
	components: {
		mpvueEcharts
	},
	data() {
		return {
			echarts: echarts,
			index1: 0,
			index2: 0,
		};
	},
	methods: {
		pieInit: function(canvas, width, height) {
			let pieChart = echarts.init(canvas, null, {
				width: width,
				height: height
			})
			canvas.setChart(pieChart)

			pieChart.setOption(getPieOption())
			return pieChart
		},
		lineInit: function(canvas, width, height) {
			let lineChart = echarts.init(canvas, null, {
				width: width,
				height: height
			})
			canvas.setChart(lineChart)

			lineChart.setOption(getLineOption())
			return lineChart
		}
	},
}
